html, body {
  height: 100%; }

body {
  color: #151F29;
  font-family: filson-pro; }

h1 {
  font-weight: 700;
  font-size: 70px; }

h2 {
  font-weight: 300;
  font-size: 40px; }

h4 {
  font-weight: 300;
  font-size: 20px; }

p {
  font-weight: 300;
  font-size: 15px; }
  p > span {
    font-weight: 500;
    color: #3A71D9; }

section {
  display: flex; }
  section:nth-of-type(even) {
    background: #F8F8FA; }

::selection {
  color: white;
  background: #3A71D9;
  /* WebKit/Blink Browsers */ }

::-moz-selection {
  color: white;
  background: #3A71D9;
  /* Gecko Browsers */ }
